.hero-img {
    height: 77vh;
    width: 100%;
    object-fit: cover;
    object-position: 5% 60%;
    filter: saturate(0); }

.hero-container {
    position: relative; }

.momento-2-arrow {
    height: 36vh; }

.momento-2-toggle {
    height: 90vh;
    position: sticky;
    top: 10.5vh; }

.rotate-90 {
    writing-mode: tb-rl;
    transform: rotate(-180deg);
    font-size: 2.3vh; }

.agora-img {
    position: absolute;
    top: 10%;
    right: 25%;
    margin: auto;
    width: 28em; }
